@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Pacifico&display=swap);
/* Reset default margins and padding for body and html */

body,
html {
  margin: 0;
  padding: 0;
  background-image: url(/naturesblessing/static/media/nature_gradient.a84d11b1.png);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Squarespace";
}

h4 {
  color: #ffe83b;
  text-shadow: 2px 4px 6px rgba(0, 0, 0, 1);
  -webkit-text-stroke: 2px rgb(84, 84, 84);
}

.nav-bar {
  display: flex;
  font-weight: 400;
  font-size: 25px;
  justify-content: space-around;
}

.primary-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center; /* to vertically center align the children */
  font-family: "Pacifico", cursive;
}

.logo {
  color: hsl(150, 100%, 67%);
  text-shadow: 1px 1px 2px rgb(0,0, 0);
  float: left;
}

p {
  color: rgb(3, 18, 0);
}

.footer {
  padding-top: 5px;
  font-size: 16px;
  display: flex;
  justify-content: center;
}

.container {
  text-align: center;
}

.container ul {
  list-style-type: none;
}

.container ul li {
  font-size: 40px;
}

.container p {
  font-size: 2.5rem;
}

.col-md-6 {
  display: flex;

  flex-direction: column;
}

/* Welcome Paragraph Styles */
.welcome {
  display: flex;
  font-size: 32px;
  font-family: "Merriweather", serif;
  -webkit-text-emphasis-color: aquamarine;
          text-emphasis-color: aquamarine;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  border-radius: 15px; /* Adds padding to text while keeping it centered */
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(36, 36, 36); /* Assuming image5.png is a dark image */
  font-weight: bold;
  text-align: justify; /* Justifying the text makes it more evenly spread */
  line-height: 1.6;
  max-width: 70%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  position: relative;
}

.packages-container {
  display: flex;
  font-size: 32px;
  font-family: "Merriweather", serif;
  /* text-emphasis-color: aquamarine; */
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  border-radius: 15px; /* Adds padding to text while keeping it centered */
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(36, 36, 36);
  font-weight: bold;
  text-align: justify; /* Justifying the text makes it more evenly spread */
  line-height: 1.6;
  max-width: 70%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  position: relative;
}

.package-picture {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto; /* this can help center the div */
}

.package-picture img {
  max-height: 100%;
  max-width: 100%;
  border-radius: 10px; /* Added 'px' to the value */
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s; /* Smooth transition for hover effect */
}

.package-picture img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05); /* Scales the image up by 5% when hovered */
}

.package-details {
  background-color: rgba(255, 255, 255, 0.8);
  margin: 10px;
}

.package-details h2 {
  margin-top: 10px; /* Add space between the image and details */
}

.package-details .Bronze {
  color: #cd7f32; /* Bronze color */
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
}

.package-details .Silver {
  color: #c0c0c0; /* Silver color */
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
}

.package-details .Gold {
  color: #ffd700; /* Gold color */
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
}

.package-details .Platinum {
  color: #e5e4e2; /* Platinum color */
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
}

.thumbnail-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75vh;
  padding: 0 15rem;
  /* Adjust this value to set the size of the circular pattern */
  margin: 1rem 0 3rem 0;
  position: relative;
}

.thumbnail-image {
  width: 1rem;
  /* Adjust this value to set the size of the individual images */
  height: 1rem;
  /* Adjust this value to set the size of the individual images */
  border-radius: 20%;
  border: 2px solid transparent;
  cursor: pointer;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.glass-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  /* White with 0.8 opacity */
  z-index: -1;
  /* Place it below the images */
}

/* Apply background color to the entire viewport for .about */
.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Center content vertically */
  align-items: center;
  /* Center content horizontally */
}

.container {
  align-items: center;
}

.container.text-center {
  display: flex;
  justify-content: center; /* Horizontally center */
  align-items: center; /* Vertically center */
  height: 100%; /* Or a specific height you want */
}

div.carousel {
  padding-top: 70px;
  margin: 0 auto;
  width: auto;
}

.about-text {
  display: flex;
  font-size: 32px;
  color: rgb(1, 127, 85);
  background-color: rgba(255, 255, 255, 0.8);
  text-shadow: -1px -1px 3 #000, 1px -1px 3 #000, -1px 1px 3 #000,
    1px 1px 3 #000;
  padding: 10px;
  border-radius: 15px; /* Adds padding to text while keeping it centered */
  background-size: cover;
  background-repeat: no-repeat;
  text-align: justify; /* Justifying the text makes it more evenly spread */
  line-height: 1.6;
  max-width: 100%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.primary-header {
  --_bg: hsla(0, 0%, 7%, 0);
  --_logo-color: hsl(150, 100%, 67%);
  --_nav-link-color: hsl(150, 100%, 67%);
  background-color: rgb(255, 255, 255);
  text-shadow: 1px 1px 4px rgb(0, 0, 0);
  padding: 1rem;

  position: relative;
  top: 0;

  z-index: 10;
  transition: 500ms ease-in;
  width: 100%;
}

.primary-header-bg {
  /* background-image: url("../images/nature_gradient.png"); */
  background-color: rgb(202, 226, 246);
  background-repeat: no-repeat;
  background-size: cover;
}

.primary-header__inner {
  display: flex;
  justify-content: space-between;
}

.primary-header .link {
  color: var(--_nav-link-color);
  text-decoration: none;
}

.primary-header .link:hover,
.primary-header .link:focus {
  opacity: 0.5;
}

.logo {
  color: var(--_logo-color);
  text-decoration: none;
  font-size: 2.5rem;
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  grid-gap: clamp(0.5rem, 5vw, 2rem);
  gap: clamp(0.5rem, 5vw, 2rem);
}

header {
  margin: 0;
  font-size: clamp(1.13rem, calc(1.06rem + 0.33vw), 1.31rem);
  line-height: 1.6;
}

.wrapper {
  width: min(65rem, 100% - 2rem);
  margin-inline: auto;
}

